let listaItems = [];

function pushx(sentence,options){
    listaItems.push({sentence,options})
}

pushx("We are ### going away for two days. It's a short trip.",["even","only","too","as well"]);//@
pushx("Give me some toast and the butter, ### please.",["as well"," only","just","also"]);//@
pushx("My wife is a diligent engineer. I am a diligent engineer ###.",["just","also","only","too"]);//@
pushx("He always wears a coat, ### in summer when it's so hot.",["only","too","just","even"]);//@
pushx("She not only sing; she ### plays the violin.",["only","as well","also","just"]);//@

export default listaItems;





